.aboutWe {
    position: relative;
    overflow: hidden;
    margin-bottom: 10px;

    img{
        position: absolute;
    transform: rotate(7deg);
    scale: 0.3;
    top: -382px;
    right: -331px;
    z-index: -1;
    }
    
    &-content {
        background-color: #fff;
        border-radius: 35px;
        max-width: 1200px;
        margin: 0 auto;
        padding: 30px;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }

   p {
        font-size: 24px;
         text-align:justify;
text-indent: 20px; 
    }

    h1 {
        font-size: 48px;
        text-align: center;
        border-bottom: solid 5px rgb(64, 131, 2);
    }

    &-card {
        display: flex;
        gap: 30px;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
    }
}