.readMore{background:#fff;width:100%;max-width:1200px;margin:0 auto;padding:20px;border-radius:30px;box-shadow:rgba(255,255,255,.35) 0px 5px 15px}.readMore p{color:#000}.readMore h1{color:#000}a{color:#b95995}a:hover{color:#b96f90}.limited{max-height:400px;overflow:hidden;position:relative}.limited.l-200{max-height:200px}.limited.l-800{max-height:800px}.limited .readMore-bottom{position:absolute;bottom:0;background:linear-gradient(to bottom, rgba(255, 255, 255, 0), rgb(255, 255, 255) 80%);width:100%;height:60px;opacity:1;transition:.3s}.read-more-checker{opacity:0;position:absolute}.read-more-checker:checked~.limited{max-height:none}.read-more-checker:checked~.limited .bottom{opacity:0;transition:.3s}.read-more-checker~.read-more-button:before{content:"Читать »"}.read-more-checker:checked~.read-more-button:before{content:"Скрыть «"}.read-more-button{cursor:pointer;display:inline-block;padding:10px;color:#000;border:1px solid #ddd;border-radius:4px}@keyframes fadeIn{from{opacity:0;transform:translate3d(0, -20%, 0)}to{opacity:1;transform:translate3d(0, 0, 0)}}