
 
 .fon-stop {
   background: rgba(0, 0, 0, 0.5);
   color: #f1f1f1;
   width: 100%;
   padding: 20px;
 }

 .main-p {
  font-size: 4.1rem;
  color: rgb(255, 0, 0);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -115%);
  text-align: center;
   
 }

 .page1 {
   position: relative;

   &-video {
     background-color: rgba(0, 0, 0, 0.8);

     height: 100vh;
     min-width: 100%;
     left: 50%;
     z-index: -1;
   }

   &-divider {
     opacity: 0.8;
   }
 }

 .arrows {
   width: 90px;
   height: 102px;
   left: 50%;
   bottom: 583px;
   margin-left: -30px;
   position: absolute;
 }

 .arrows path {
   stroke: #ffffff;
   fill: transparent;
   stroke-width: 1px;
   animation: arrow 2s infinite;
   -webkit-animation: arrow 2s infinite;
 }

 @keyframes arrow {
   0% {
     opacity: 0
   }

   40% {
     opacity: 1
   }

   80% {
     opacity: 0
   }

   100% {
     opacity: 0
   }
 }

 @-webkit-keyframes arrow

 /*Safari and Chrome*/
   {
   0% {
     opacity: 0
   }

   40% {
     opacity: 1
   }

   80% {
     opacity: 0
   }

   100% {
     opacity: 0
   }
 }

 .arrows path.a1 {
   animation-delay: -1s;
   -webkit-animation-delay: -1s;
   /* Safari 和 Chrome */
 }

 .arrows path.a2 {
   animation-delay: -0.5s;
   -webkit-animation-delay: -0.5s;
   /* Safari 和 Chrome */
 }

 .arrows path.a3 {
   animation-delay: 0s;
   -webkit-animation-delay: 0s;
   /* Safari 和 Chrome */
 }